@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@300;400&family=Roboto:wght@300;400&family=Source+Sans+Pro:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #ECECEC;
}
.navbar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
  background-color: #fff;
  padding: 15px 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: 'Source Sans Pro', Sans-Serif;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  color: gray;
  margin-bottom: 10px;
}

.navbar .navbar_left {
  display: flex;
  align-items: center;
}

.navbar_left .burger_icon {
  margin-right: 10px;
}

.navbar .navbar_middle {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex: .9 1;
  border: 1px solid lightgray;
  border-radius: 3px;
}

.navbar .navbar_middle .navbar_searchInput {
  flex: .95 1;
  border: none;
  border-right: 1px solid lightgray;
  padding: 5px;
  font-family: 'Montserrat', Sans-Serif;
}

.navbar_searchInput:focus {
  outline: none;
}

.navbar .navbar_middle .navbar_searchIcon {
  flex: 0.2 1;
}

.navbar_right {
  font-size: 24px;
}
.sideBar {
  position: absolute;
  left: 0;
  width: 60vw;
  height: 85vh;
  background-color: #fff;
  z-index: 990;
  margin-top: -10px;
  padding-top: 15px;
  font-family: 'PT Sans Narrow', Sans-Serif;
  color: gray;
  padding: 10px 5px;
  box-shadow: 1px 0 8px rgba(0, 0, 0, 0.1);
}

.sideBar > h3 {
  font-family: 'Source Sans Pro', Sans-Serif;
  font-weight: normal;
  margin-bottom: 10px;
}

.sideBar .divider {
  width: 100%;
  height: .5px;
  background-color: lightgray;
  margin: 0 auto;
  margin-bottom: 10px;
}

.sideBar .sideBar_links {
  width: 100%;
  list-style: none;
  padding: 0 5px;
}

.sideBar_links .sideBar_linkItem {
  margin-bottom: 15px;
  border-bottom: .3px solid lightgray;
  padding: 5px;
  letter-spacing: 1px;
}

.sideBar .sideBar_linkItem .fontAwesomeIcon {
  margin-right: 5px;
}

.sideBar .link_to {
  text-decoration: none;
  color: gray;
}
.video {
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 auto;
  background-color: #fff;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
}

.video .divider {
  display: block;
  width: 100%;
  height: 0.5px;
  background-color: lightgray;
  margin-bottom: 7px;
}

.video .video_thumbnail {
  width: 100%;
  object-fit: contain;
  border-radius: 5px;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.2),
  -2px -2px 8px rgba(0, 0, 0, 0.2);
}

.video .video_videoInfo {
  margin-top: 10px;
  font-family: 'PT Sans Narrow', Sans-Serif;
}

.video .video_videoInfo .video_owner {
  margin-bottom: 7px;
  letter-spacing: 1px;
}

.badge {
  text-transform: uppercase;
  font-weight: bold;
  color: white;
  font-size: 12px;
  letter-spacing: 1px;
  background-color: #007bff;
  margin-right: 5px;
  padding: 1px 5px;
  border-radius: 2px;
}

.video_videoInfo > p {
  margin-bottom: 7px;
}

.video_videoInfo > p:last-child {
  margin-bottom: 0;
}
